	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"One Point Oh","date":"09 Nov 2004","slug":"/2004-11-09-one-point-oh/","tags":"Y2004"},"html":"<p>Despite my reservations about the release, I of course downloaded <a title=\"Firefox - Rediscover the web\" href=\"http://www.mozilla.org/products/firefox/\">Firefox 1.0</a> as soon as it was available. And lo and behold, the <a href=\"http://www.papascott.de/archives/2004/11/08/firefox-not-ready/\">hesitation</a> on <a href=\"http://jsiwwe.jsjjeidnhwnnds.com/\">bad hostnames</a> under Linux has been fixed (it passes the request to the proxy server immediately instead of waiting for a DNS lookup). </p>\n<p>I still don't get all gushy about a web browser though. It's just a tool.</p>"},"next":{"fields":{"title":"","date":"09 Nov 2004","slug":"/2004-11-09-deutsche-nationale-regierungsumsturzenstag/"}},"previous":{"fields":{"title":"Singles night at Wal-Mart in Germany","date":"10 Nov 2004","slug":"/2004-11-10-singles-night-at-walmart-in-germany/"}}},{"node":{"fields":{"title":"","date":"09 Nov 2004","slug":"/2004-11-09-deutsche-nationale-regierungsumsturzenstag/","tags":"Y2004"},"html":"<p><em><a title=\"A Fistful of Euros: A very German day\" href=\"http://fistfulofeuros.net/archives/000918.php#7240\">Deutsche Nationale Regierungsumstürzenstag</a></em></p>"},"next":{"fields":{"title":"A Nation of Pyromaniacs","date":"09 Nov 2004","slug":"/2004-11-09-a-nation-of-pyromaniacs/"}},"previous":{"fields":{"title":"One Point Oh","date":"09 Nov 2004","slug":"/2004-11-09-one-point-oh/"}}},{"node":{"fields":{"title":"A Nation of Pyromaniacs","date":"09 Nov 2004","slug":"/2004-11-09-a-nation-of-pyromaniacs/","tags":"Y2004"},"html":"<p>Last night it occurred to me that a country where they let little children run around in the dark with lit candles in paper lanterns, where they put the same lit candles in dried-out pine trees indoors for Christmas, and where they have a holiday that combines alcohol and hand-held fireworks, that such a country has a lot of gall calling <em>Americans</em> stupid for electing Bush.</p>"},"next":{"fields":{"title":"Happy Cadaver Day","date":"08 Nov 2004","slug":"/2004-11-08-happy-cadaver-day/"}},"previous":{"fields":{"title":"","date":"09 Nov 2004","slug":"/2004-11-09-deutsche-nationale-regierungsumsturzenstag/"}}}],"pathPrefix":"","first":false,"last":false,"index":386,"pageCount":1101}}

